import React from "react";

function CardComponent(course) {
  return (
    <div className="card">
      <img src="images/course1.jpg" className="course-image u-full-width" alt="course"></img>
      <div className="info-card">
        <h4>HTML5, CSS3, JavaScript basics</h4>
        <p>Maksym Cherniaiev</p>
        <img src="images/stars.png" alt="stars"></img>
        <div className="price-info">
          <span className="price">$200</span>
          <span className="current-price">$15</span>
        </div>
        <a href="#" className="u-full-width button-primary button input add-to-cart" data-id="1">Add to Cart</a>
      </div>
    </div>
  );
}

export default CardComponent;